@import "src/styles/variables";

.root {
    height: 100vh;
}

.app {
    min-height: 100%;
    background: $color-bg-blue;
    max-width: 1000px;
    margin: auto;
}

.loading {
    width: 100%;
    max-width: 1000px;
    position: absolute;
    transform: translateY(-50%);
    top: 50%;
    text-align: center;
    color: $color-light-white;
}

.loader {
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    background: white;
    color: $color-light-white;
}

.stonks {
    font-size: 2.5rem;
    &_up {
        color: $color-green;
    }
    &_down {
        color: $color-red;
    }
    &_static{
        color: $color-light-dark;
    }
}

.spinning {
    padding: 1rem 0 5rem 0;
}

.pullToRefresh {
    padding: 1rem 0 0;
    background: $color-white;
    color: $color-bg-blue;
    background: $color-bg-blue;
}

.ptr {
    height: fit-content;
    overflow: visible;
}